.checkbox-label {
  position: relative;
  cursor: pointer;
  padding-left: rem(35);
  input {
    appearance: none;
    position: absolute;

    &:checked + .checkbox {
      background-image: url('../img/svg/custom-checkbox-checked.svg');
    }
  }
}

.checkbox {
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  background-image: url('../img/svg/custom-checkbox-unchecked.svg');
  background-size: contain;
}
